<template>
  <div class="order">
    <!-- 我的订单主要内容 -->
    <div class="order-content" v-if="User!==null">
      <div class="content" v-for="(item,index) in User" :key="index">
        <ul>
          <!-- 我的订单表头 -->
    
          <li class="header">
            <div class="pro-img"></div>
            <div class="pro-name">店铺名称</div>
            <div class="pro-price">店主昵称</div>
            <div class="pro-total">注册时间</div>
            <div class="pro-operate">操作</div>
          </li>
          <!-- 我的订单表头END -->

          <!-- 订单列表 -->
          <li class="product-list"  >
            <div class="pro-img">
                 <img :src="User[index].goodsCoverImg" />
             </div>
            <div class="pro-name">
                {{User[index].storeName}}
 
            </div>
            <div class="pro-price"> 杀杀杀 </div>
            <div class="pro-total">{{User[index].createTime}}</div>
           
            <div class="pro-operate">
               <el-dialog title="表单弹框" :visible.sync="dialogVisible" width="30%">
                  <el-form ref="form" :model="form" label-width="80px">
                    <el-form-item label="店铺名" >
                      <el-input v-model="form.storeName" ></el-input>
                    </el-form-item>
                    <el-form-item label="描述">
                      <el-input v-model="form.introduceSign"></el-input>
                    </el-form-item>
                  </el-form>
                  <span slot="footer" class="dialog-footer">
                    <el-button @click="dialogVisible = false">取 消</el-button>
                    <el-button type="primary" @click="undateshop()">确 定</el-button>
                  </span>
                </el-dialog>
              <el-button type="primary" size="mini"  @click="delShop(User[index].storeId)" >删除</el-button>
              <el-button type="success" size="mini"   @click="undateshopID( index )" >修改</el-button>
 
              <p v-show="User[index].orderStatus == 3">订单完成</p>
            </div>
    </li>
        </ul>
   
 
          <!-- 订单列表END -->
        </div>
      </div>
      <div style="margin-top:-40px;"></div>
    </div>
    <!-- 我的订单主要内容END -->

    <!-- 订单为空的时候显示的内容 -->
    <!-- <div v-else class="order-empty">
      <div class="empty">
        <h2>您的订单还是空的！</h2>
        <p>快去购物吧！</p>
      </div>
    </div> -->
    <!-- 订单为空的时候显示的内容END -->
  <!-- </div> -->
</template>

<script>
export default {
  data(){
    return{
      changeID:-1,
      dialogVisible:false,
      User:"",
        form: {
 
          storeName:'',
          introduceSign:'',

        },
    }

  },
    created(){
      this.getPromo()

    },
      watch: {
      changeID: function(val){
          this.form.storeName = this.User[val].storeName,
          this.form.introduceSign= this.User[val].introduceSign,
          console.log(val);
          if(val >=0)
           this.dialogVisible = true
      }
    },
    methods:{
        undateshopID(val){
        this.changeID=val 
        console.log("this.changeID=val",this.changeID);
        },
      undateshop(){
        console.log(this.User[this.changeID].storeId,this.form.storeName,this.form.introduceSign ,);
       this.dialogVisible = false
       this.changeID=-1
       this.$axios(   
            {
            //  url: "http://localhost:3000/cart", 
           url: this.$target+ "/admin/storesInfo/update",
              method: 'post',
              params:{
                storeId:this.User[this.changeID].storeId ,
                storeName:this.form.storeName,
                introduceSign:this.form.introduceSign,
 
              }
        })
        .then(res => {
          if(res.data.flag){
                this.notifySucceed(res.data.msg);
            }
            else{
              this.notifyError("更新订单失败",res.data.msg);
            }
          })
        .catch(err => {
          return Promise.reject(err);
        });   this.$router.go(0)
    },
        delShop(val){
            this.$axios(   
            {
                // url:  " http://localhost:3000/shop", 
              url: this.$target+"/admin/storesInfo/delete", 
              method: 'post',
              params:{
                    storeId:val
              }
            })
            .then(res => {
             this.notifySucceed(res.data.msg);
             })
            .catch(err => {
            return Promise.reject(err);
            });     

        },
       getPromo( ) {
       this.$axios(   
            {
               url: this.$target+"/admin/storesInfo", 
              method: 'get',
              params:{
    
              }
            })
            .then(res => {
            this.User = res.data.data;
            console.log("this.User ",this.User );
            })
            .catch(err => {
            return Promise.reject(err);
            });     
    }
  }
}
</script>

<style scoped>
.order {
  background-color: #f5f5f5;
  padding-bottom: 20px;
}
/* 我的订单头部CSS */
.order .order-header {
  height: 64px;
  border-bottom: 2px solid #ff6700;
  background-color: #fff;
  margin-bottom: 20px;
}
.order .order-header .order-header-content {
  width: 1225px;
  margin: 0 auto;
}
.order .order-header p {
  font-size: 28px;
  line-height: 58px;
  float: left;
  font-weight: normal;
  color: #424242;
}
/* 我的订单头部CSS END */
.order .content {
  width: 1225px;
  margin: 0 auto;
  background-color: #fff;
  margin-bottom: 50px;
}

.order .content ul {
  background-color: #fff;
  color: #424242;
  line-height: 85px;
}
/* 我的订单表头CSS */
.order .content ul .order-info {
  height: 60px;
  line-height: 60px;
  padding: 0 26px;
  color: #424242;
  border-bottom: 1px solid #ff6700;
}
.order .content ul .order-info .order-id {
  float: left;
  color: #ff6700;
}
.order .content ul .order-info .order-time {
  float: right;
}

.order .content ul .header {
  height: 90px;
  padding-right: 26px;
  color: #424242;
}
/* 我的订单表头CSS END */

/* 订单列表CSS */
.order .content ul .product-list {
  height: 85px;
  padding: 15px 26px 15px 0;
  border-top: 1px solid #e0e0e0;
}
.order .content ul .pro-img {
  float: left;
  height: 85px;
  width: 120px;
  padding-left: 80px;
  padding-right: 10px;
}
.order .content ul .pro-img img {
  height: 80px;
  width: 80px;
}
.order .content ul .pro-name {
  float: left;
  width: 100px;
    padding-right: 30px;

}
.order .content ul .pro-name a {
  color: #424242;
}
.order .content ul .pro-name a:hover {
  color: #ff6700;
}
.order .content ul .pro-price {
  float: left;
  width: 100px;
  padding-right: 80px;
  text-align: center;
}
.order .content ul .pro-operate {
  float: right;
  width: 200px;
  padding-left: 100px;
  text-align: center;
}
.order .content ul .pro-num {
  float: left;
  width: 190px;
  text-align: center;
}
.order .content ul .pro-total {
  float: left;
  width: 200px;
  padding-right: 70px;
  text-align: center;
}
.order .content ul .pro-total-in {
  color: #ff6700;
}

.order .order-bar {
  width: 1185px;
  padding: 0 20px;
  border-top: 1px solid #ff6700;
  height: 50px;
  line-height: 50px;
  background-color: #fff;
}
.order .order-bar .order-bar-left {
  float: left;
}
.order .order-bar .order-bar-left .order-total {
  color: #757575;
}
.order .order-bar .order-bar-left .order-total-num {
  color: #ff6700;
}
.order .order-bar .order-bar-right {
  float: right;
}
.order .order-bar .order-bar-right .total-price-title {
  color: #ff6700;
  font-size: 14px;
}
.order .order-bar .order-bar-right .total-price {
  color: #ff6700;
  font-size: 30px;
}
/* 订单列表CSS END */

/* 订单为空的时候显示的内容CSS */
.order .order-empty {
  width: 1225px;
  margin: 0 auto;
}
.order .order-empty .empty {
  height: 300px;
  padding: 0 0 130px 558px;
  margin: 65px 0 0;
  background: url(../assets/imgs/cart-empty.png) no-repeat 124px 0;
  color: #b0b0b0;
  overflow: hidden;
}

.order .order-empty .empty h2 {
  margin: 70px 0 15px;
  font-size: 36px;
}
.order .order-empty .empty p {
  margin: 0 0 20px;
  font-size: 20px;
}
/* 订单为空的时候显示的内容CSS END */
</style>